@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@100;400;500;600;800&display=swap');

body {
    font-family: 'Montserrat', sans-serif;
    margin: 0px;
    background-color: #000;
    color: #FFF;
    height: 100vh;
    font-weight: 500;
}

img {
    border-radius: 20px;
}

.texto-destaque {
    color: #7bebcf;
}

a {
    text-decoration: none;
    color: #4059D7;
}

.textGradient {
    display: inline-block;
    background: rgb(123, 235, 207);
    background: linear-gradient(60deg, rgba(123, 235, 207, 1) 0%, rgba(30, 120, 255, 1) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin: 0px;
}

.borderButtonGradient {
    display: block;
    background: rgb(123, 235, 207);
    background: linear-gradient(60deg, rgba(123, 235, 207, 1) 0%, rgba(30, 120, 255, 1) 100%);
    border-radius: 10px;
    padding: 2px;
    width: 100%;
    margin: 20px auto;
    transition: all .2s ease-in-out;
}

.borderButtonGradient:hover {
    transform: translate(0px, -3px);
    display: block;
    transition: all .2s ease-in-out;
}

.borderButtonGradient div {
    background-color: #000;
    color: #FFF;
    border-radius: 10px;
    padding-block: 6px;
    border: none;
    cursor: pointer;
}

.borderButtonGradient div p {
    margin: 0px;
    text-align: left;
    padding-left: 10px;
    display: flex;
    align-items: center;
}

main#root {
    width: 1200px;
    display: flex;
    height: calc(100vh - 50px);
    max-width: 100%;
    margin: auto;
    align-items: center;
}

main .App {
    display: flex;
    gap: 10px;
    justify-content: space-between;
}

.HelloPictureAndText {
    display: flex;
    max-width: calc(70% - 50px);
    align-items: center;
    gap: 20px;
    background-color: #323232;
    border-radius: 20px;
    padding: 20px;
}

.HelloPictureAndText h1 {
    font-size: 5rem;
    line-height: 1;
    margin: 10px 0px;
    margin-top: 0px;
    display: table;
    background: rgb(123, 235, 207);
    background: linear-gradient(60deg, rgba(123, 235, 207, 1) 0%, rgba(30, 120, 255, 1) 100%);
    -webkit-background-clip: text;
    background-clip: text;
    -webkit-text-fill-color: transparent;
}

.HelloPictureAndText article p:last-child {
    margin-bottom: 0px;
}

.ContactInfo {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-content: center;
    align-items: center;
    width: 100%;
    max-width: calc(30% - 50px);
    text-align: center;
    background-color: #323232;
    border-radius: 20px;
    padding: 0px 20px;
}

.ContactInfo a {
    width: 250px;
    display: block;
    max-width: 100%;
}

.ContactInfo div p span {
    filter: invert(1);
    padding: 7px;
    margin-right: 10px;
}

#Icon-Site div p span {
    filter: unset;
    font-weight: 800;
    padding: 0px 4px 0px 3px;
}

