footer {
    position: fixed;
    bottom: 0px;
    text-align: center;
    width: 100%;
}

footer a {
    font-weight: 600;
}

footer a.politica-de-privacidade {
    font-weight: 400;
    color: #FFF;
}

footer small {
    display: block;
    margin: auto;
    padding: 10px 0px;
    padding-bottom: 20px;
    width: 1200px;
    max-width: calc(100% - 10px);
    background-color: #323232;
    border-radius: 20px 20px 0px 0px;
}

@media only screen and (max-width: 1000px) {
    body {
        height: auto;
    }

    main#root {
        height: auto;
    }

    main .App {
        display: flex;
        gap: 0px;
        justify-content: space-between;
        align-items: center;
    }

    .HelloPictureAndText {
        display: block;
        max-width: 50%;
        margin: 5px;
    }

    img {
        width: 250px;
    }

    .HelloPictureAndText article {
        margin-top: 20px;
    }

    .ContactInfo {
        width: 50%;
        max-width: 100%;
        text-align: -webkit-center;
        margin: 10px;
        height: calc(100vh - 90px);
    }
}

@media only screen and (max-width: 690px) {
    main .App {
        display: block;
    }

    .HelloPictureAndText {
        display: block;
        max-width: 100%;
        margin: 0px;
        padding: 0px;
        background-color: unset;
        text-align: center;
    }

    img {
        width: 100%;
        border-radius: unset;
    }

    .HelloPictureAndText article {
        text-align: -webkit-center;
        padding: 20px;
        margin-top: -80px;
        position: relative;
        background: rgb(0, 0, 0);
        background: linear-gradient(0deg, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0) 100%);
    }

    .HelloPictureAndText h1 {
        font-size: 2rem;
    }

    .ContactInfo {
        display: block;
        padding: 20px;
        width: 80%;
        max-width: calc(100% - 55px);
        text-align: -webkit-center;
        margin: 10px auto;
        height: auto;
    }

    footer {
        position: unset;
    }
}